import { Component, Input, OnInit, ViewEncapsulation, ViewChild, ChangeDetectorRef } from '@angular/core';
import { ActivatedRoute, Router, NavigationEnd } from '@angular/router';

import { Observable, BehaviorSubject } from 'rxjs';
import { filter, map, startWith, debounceTime, takeUntil } from 'rxjs/operators';
import { FormControl } from '@angular/forms';
import { Subscription, Subject } from 'rxjs';

@Component({
  selector: 'app-appointment-event-types',
  templateUrl: './appointment-event-types.component.html',
  styleUrls: ['./appointment-event-types.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class AppointmentEventTypesComponent implements OnInit {

  event:any = {}
  roleData:any = []
  staffData:any = []

  eventsData:any = []

  _eventsDataList:any;
  fullEventsDataList:any = [];

  searchData:FormControl;

  search:string = '';
  deleteSubscribe:Subscription;

  ItemStartIndex: any = 0;
  ItemEndIndex: any;
  totalItems: any;
  itemLimit: any = 5;

  allData: any;

  message: any;

  constructor() {

    this._eventsDataList = new BehaviorSubject(null);
    this.searchData = new FormControl('');

  }

  get events$(): Observable<any>{
    return this._eventsDataList.asObservable();
  }

  getSelectedRole(event) {
    this.event.roleId = event[0].id
  }

  getSelectedStaff(event) {
    this.event.staffId = event[0].id
  }

  selectEvent(event) {

  }

  ngOnInit(): void {

    this.roleData = [
      {
        id: 1,
        name: 'Master'
      },
      {
        id: 2,
        name: 'Student'
      }
    ]

    this.staffData = [
      {
        id: 1,
        name: 'Test 01'
      },
      {
        id: 2,
        name: 'Test 01'
      }
    ]

   let res = [
      {
        id: 1,
        name: '15 Minutes Meeting',
        url: '/15mins',
        type: 'One-on-One',
        duration: '15 mins'
      },
      {
        id: 2,
        name: '20 Minutes Meeting',
        url: '/20mins',
        type: 'One-on-One',
        duration: '20 mins'
      }
    ]

    this.fullEventsDataList = res;
    this.allData = res;
    this._eventsDataList.next(res);

    this.events$.pipe( filter(res => res != null),
    ).subscribe((res:any) => {

      this.totalItems = res.length;
      this.ItemStartIndex = 0;

      if (this.totalItems > this.itemLimit) {
        this.ItemEndIndex = this.itemLimit;
      }
      else {
        this.ItemEndIndex = this.totalItems;
      }

    })

    // Subscribe to search input field value changes
    this.searchData.valueChanges
    .pipe(
      startWith(''),
      map((val) => {

        let newData = this.fullEventsDataList.filter(item => {
          for (let field in item) {
            if (item[field] === null || item[field] === undefined) {
              continue;
            }
            if (item[field].toString().toLowerCase().indexOf(val.toString().toLowerCase()) !== -1) {
              return item;
            }
          }
        })

        this._eventsDataList.next(newData);


      })
    )
    .subscribe();

  }

}
